import {Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder ,Validators } from '@angular/forms';
import {IntegrationService} from './../../../shared/services/integration.service';
declare var window: any;
declare var jQuery: any;

@Component({
    selector: 'app-og-integrations',
    template: require('./integrations.component.html'),
    styles: [require('./integrations.component.css'), require('./../../../../assets/css/sahil-hover.css'), require('./../../../../assets/css/custom-material.css')]
})

export class IntegrationsComponent implements OnInit {
   company: String = '';
   addKey: FormGroup;
   addMarketo: FormGroup;
   testMarketoForm: FormGroup;
   error: boolean = false;
   errorMessage: string;
   isSyncEnable: boolean = false;
   marketType: string = '';
   leadscount: string;
   isHubspotConnected: boolean = false;
   isMarketoConnected: boolean = false;
   configuration: any = {
        salesforce: false,
        marketo: false
   };
   constructor(private _integrationService: IntegrationService, private fb: FormBuilder) {
        this.company = localStorage.getItem('company');
   }

   ngOnInit() {
       this.isSyncEnable = false ;
       this.getAllConfiguration();
       this.getsyncLead();
       this.addKey = this.fb.group({
           hapikey : ['', Validators.compose([Validators.required])]
       });

       this.addMarketo = this.fb.group({
           client_id : ['', Validators.compose([Validators.required])],
           client_secret : ['', Validators.compose([Validators.required])],
           ideantity : ['', Validators.compose([Validators.required])]
       });

      this.testMarketoForm = this.fb.group({
        firstName : [''],
        email : ['', Validators.compose([Validators.required])],
        lastName : ['']
      });

       jQuery.material.init();
   }

   connectSalesforce() {
       let data = {
            clientId : '3MVG9ZL0ppGP5UrAohWOdEpilPP.lPzCvEFoE0JAS_z.jPfRg58WEm_sW1aFo6bWaGRGlhq3fEi67ZDtdOw1d',
            clientSecret : '7806485071355471118',
            company : this.company
        };
        this._integrationService.getLink(data, 'salesforce')
        .subscribe((response) => {
             window.location.href = response;
        }, (error) => {
            console.log(error);
        });
   }

    getAllConfiguration() {
    this._integrationService.getAllConfiguration()
      .subscribe((result) => {
        this.configuration.salesforce = result.salesforce ? result.salesforce : false;
        this.configuration.marketo = result.marketo ? result.marketo : false;
        console.log(this.configuration);
      }, (error) => {
        console.log(error);
      });
  }

  getsyncLead() {
      this._integrationService.getCompanySyncLeads()
      .subscribe((result) => {
       this.leadscount = result.marketo + ' item';
       if (this.leadscount >= '1') {
           this.isSyncEnable = true;
       }
      }, (error) => {
        console.log(error);
      });
  }

  syncLead() {
      jQuery('#sync-leads').html('Please Wait..');
      this._integrationService.syncLeads('marketo')
      .subscribe((result) => {
          jQuery('#sync-leads').html('Sync');
          this.isSyncEnable = false;
          this.getsyncLead();
           window.toastNotification('Leads will synced to connected Marketo account fo this company');
      }, (error) => {
          this.isSyncEnable = true;
          jQuery('#sync-leads').html('Sync');
          window.toastNotification('Some error occured!! Please Reconfigure Marketo');
      });
  }

//    connectHubspot() {
//        jQuery('#btnSave').html('please Wait...').attr('disabled', true);
//        let data = {
//            hapikey : this.addKey.value.hapikey,
//            company : this.company


//        };
//        this._integrationService.connectHubspot(data)
//         .subscribe((response) => {
//             console.log(response);
//             jQuery('#add-new-hapikey').modal('hide');
//             window.toastNotification('You have Successfully added Hubspot for integration ');
//             this.gethubSpot();
//         }, (error) => {
//             this.error = true;
//             this.errorMessage = error.error.err_message;
//             console.log(error);
//              jQuery('#btnSave').html('Save Hapikey').attr('disabled', false);
//         });
//    }


   openModal() {
       jQuery('#add-new-hapikey').modal('show');
   }
   openMarketo() {
        jQuery('#add-marketo').modal('show');
   }
  testMarketo(){
      jQuery('#test-marketo').modal('show');
  }
   connectMarketo() {
       jQuery('#btnmMarketo').html('please Wait...').attr('disabled', true);
        let data = {
            client_id : this.addMarketo.value.client_id,
            client_secret : this.addMarketo.value.client_secret,
            restEndpoint : this.addMarketo.value.ideantity,
            company : this.company
        };
       this._integrationService.authorization(data, 'marketo')
        .subscribe((response) => {
            if(response.error) {
                this.error = true;
                this.errorMessage = response.error_description;
                jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
             }else {
                this.error = false;
                jQuery('#add-marketo').modal('hide');
                jQuery('#add-marketo input').val('');
                jQuery('#btnmMarketo').html('Reconfigure Marketo').attr('disabled', false);
                this.configuration.marketo = true;
                window.toastNotification('You have Successfully added Marketo for integration, Please test it by clicking on Test Connection.');
            }
        }, (error) => {
            if (error.error.code === 'ENOTFOUND') {
                 this.errorMessage = 'Authentication error - Please re-enter the end points';
            }else {
                this.errorMessage = error.error.message;
            }
            this.error = true;
            jQuery('#btnmMarketo').html('Connect Marketo').attr('disabled', false);
        });
   }
   change() {
       this.error = false;
   }


   // gethubSpot() {
   //      this._integrationService.geHubspotData(this.company)
   //      .subscribe((response) => {
   //         this.isHubspotConnected = true;
   //      }, (error) => {
   //          console.log(error);
   //      });
   // }

   testPopup(type: string) {
          this.marketType = type;
          jQuery('#test-marketo').modal('show');
    }

    testSaveLead(type: string) {
        let data = {
            firstName : this.testMarketoForm.value.firstName,
            lastName : this.testMarketoForm.value.lastName,
            email : this.testMarketoForm.value.email,
            company : this.company
            };
       jQuery('#btn-test-Marketo').html('please Wait...').attr('disabled', true);
       this._integrationService.testSaveLead(data, type)
            .subscribe((response) => {
              if(response.success){
                if (response.result[0].reasons) {
                    this.error = true;
                    this.errorMessage = response.result[0].reasons[0]['message'];
                    jQuery('#btn-test-Marketo').html('Test').attr('disabled', false);
                }else if (response.result[0]['status'] === 'created') {
                    this.error = false;
                    jQuery('#test-marketo').modal('hide');
                    window.toastNotification('test Successfull');
                    jQuery('#btn-test-Marketo').html('Test').attr('disabled', false);
                }
            }else {
                this.error = true;
                this.errorMessage = response.errors[0]['message'];
                jQuery('#btn-test-Marketo').html('Test').attr('disabled', false);
            }
            }, (error) => {
              jQuery('#btn-test-Marketo').html('Test').attr('disabled', false);
            });
    }


}





